import React, { useEffect, useState } from 'react';
import { auth, db } from './firebase';
import { doc, getDoc, collection, query, where, getDocs } from 'firebase/firestore';

function Dashboard() {
  const [subscription, setSubscription] = useState(null);
  const [appointments, setAppointments] = useState([]);
  const [loading, setLoading] = useState(true);

  useEffect(() => {
    const fetchData = async () => {
      const user = auth.currentUser;
      console.log(" Utilisateur connect :", user);

      if (!user) {
        console.log(" Aucun utilisateur connect.");
        setLoading(false);
        return;
      }

      try {
        const subRef = doc(db, 'subscriptions', user.uid);
        const subSnap = await getDoc(subRef);
        if (subSnap.exists()) {
          setSubscription(subSnap.data());
        } else {
          console.log(" Aucun abonnement trouv");
        }

        const q = query(collection(db, 'appointments'), where('userId', '==', user.uid));
        const snapshot = await getDocs(q);
        const results = snapshot.docs.map(doc => ({
          id: doc.id,
          ...doc.data()
        }));
        setAppointments(results);
      } catch (err) {
        console.error(" Erreur Dashboard:", err);
      } finally {
        setLoading(false);
      }
    };

    fetchData();
  }, []);

  if (loading) {
    return <p style={{ padding: '20px' }}> Chargement...</p>;
  }

  return (
    <div style={{ padding: '20px' }}>
      <h2> Bienvenue sur ton Dashboard</h2>

      <section>
        <h3> Mon Abonnement</h3>
        {subscription ? (
          <ul>
            <li><strong>Type :</strong> {subscription.type}</li>
            <li><strong>Statut :</strong> {subscription.status}</li>
            <li><strong>Prochain paiement :</strong> {subscription.nextPaymentDate}</li>
          </ul>
        ) : <p>Aucun abonnement trouv.</p>}
      </section>

      <section>
        <h3> Mes Rendez-vous</h3>
        {appointments.length > 0 ? (
          <ul>
            {appointments.map(app => (
              <li key={app.id}>{app.date}  {app.time}</li>
            ))}
          </ul>
        ) : <p>Aucun rendez-vous.</p>}
      </section>
    </div>
  );
}

export default Dashboard;

