import React, { useState } from 'react';
import { Routes, Route } from 'react-router-dom'; //  ne pas importer BrowserRouter ici
import { useTranslation } from 'react-i18next';
import './i18n';
import './App.css';

//  Composants
import Navbar from './Navbar';
import PrivateRoute from './PrivateRoute';

//  Pages
import Home from './Home';
import About from './About';
import Projects from './Projects';
import Shop from './Shop';
import Dashboard from './Dashboard';
import ResetPassword from './ResetPassword';

//  Auth
import Login from './Login';
import Register from './Register';

//  Services
import LLC from './LLC';
import Website from './Website';
import MobileApp from './MobileApp';
import Ads from './Ads';
import Logo from './Logo';
import Digital from './Digital';

function App() {
  const [showLogin, setShowLogin] = useState(true);
  const { t, i18n } = useTranslation();

  return (
    <div className="App">
      <div style={{ textAlign: 'center', marginTop: '10px' }}>
        <button onClick={() => i18n.changeLanguage('fr')} style={{ marginRight: '10px' }}>FR</button>
        <button onClick={() => i18n.changeLanguage('en')}>EN</button>
      </div>

      <Navbar />

      <Routes>
        <Route path="/" element={<Home />} />
        <Route path="/about" element={<About />} />
        <Route path="/projects" element={<Projects />} />
        <Route path="/shop" element={<Shop />} />
        <Route path="/reset-password" element={<ResetPassword />} />

        {/*  Route protge */}
        <Route path="/dashboard" element={
          <PrivateRoute>
            <Dashboard />
          </PrivateRoute>
        } />

        {/* Auth */}
        <Route
          path="/auth"
          element={
            <>
              {showLogin ? <Login /> : <Register />}
              <p style={{ textAlign: 'center', marginTop: '20px' }}>
                {showLogin ? t("no_account") : t("already_account")}{' '}
                <button onClick={() => setShowLogin(!showLogin)}>
                  {showLogin ? t("register") : t("login")}
                </button>
              </p>
            </>
          }
        />

        {/* Services */}
        <Route path="/services/llc" element={<LLC />} />
        <Route path="/services/web" element={<Website />} />
        <Route path="/services/app" element={<MobileApp />} />
        <Route path="/services/ads" element={<Ads />} />
        <Route path="/services/logo" element={<Logo />} />
        <Route path="/services/digital" element={<Digital />} />
      </Routes>
    </div>
  );
}

export default App;



